<template>
  <div>
    <v-card>
        <v-card-title>
            <v-btn @click="refresh">
                Refresh
            </v-btn>

            <v-spacer />

            <v-text-field
                v-model="search"
                append-icon="mdi-magnify"
                label="Search"
                single-line
                hide-details
            ></v-text-field>
        </v-card-title>
        <v-data-table
            :headers="headers"
            :items="peoples"
            :search="search"
            hide-default-footer
        ></v-data-table>
    </v-card>
  </div>
</template>

<script>
import axios from '../main'
export default {
    data: () => ({
        headers: [
            {
                text: 'Number',
                align: 'start',
                sortable: true,
                value: 'num',
            },
            {
                text: 'Name',
                sortable: false,
                value: 'name',
            },
            {
                text: 'Kill',
                sortable: true,
                value: 'kill',
            },
            {
                text: 'Group',
                sortable: true,
                value: 'group'
            },
            {
                text: 'Time',
                sortable: true,
                value: 'time'
            }
        ],
        peoples: [
            {
                num: 3,
                name: 'BGG',
                kill: 5,
                group: 1,
                time: '2021/1/23 10:20:21'
            },
            {
                num: 4,
                name: 'HII',
                kill: 1,
                group: 2,
                time: '2021/1/30 10:20:21'
            },
            {
                num: 20,
                name: 'HELLO',
                kill: 4,
                group: 5,
                time: '2021/1/23 10:10:21'
            }
        ],
        search: '',
    }),
    methods: {
        refresh () {
            let self = this
            this.axios.get('/rank')
                .then(function (response) {
                    var data = response.data
                    self.peoples = data
                })
                .catch(function (response) {
                    console.log(response)
                })
        }
    },
    beforeRouteEnter (to, from, next) {
		axios.get("/rank")
        .then(response => {
            next(vm => {
                vm.peoples = response.data;
            })
        })
        .catch(response => {
            console.log(response);
        })
    }
}
</script>

